.todo-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;
    gap: 10px;

    width: 100%;
    margin: 10px 0;
    border-radius: 5px;
    padding: 10px;
}

.todo-form input {
    width: 100%;
    padding: 10px;
}

.todo-form input:focus , .todo-form textarea:focus {
    outline: none;
    border-bottom: 2px solid rgb(0, 128, 255);
}

.todo-form textarea {
    width: 100%;
    padding: 10px;
}

.todo-form button {
    font-size: 20px;
    width: 100%;
    padding: 10px 8px;

    border: none;
    outline: none;

    cursor: pointer;

    color: white;
    background-color: rgb(74, 74, 74);

    border: 2px solid white;
}

.todo-form button:hover {
    background-color: black;
}