.header {
    width: 100%;
    padding: 20px;

    position: absolute;
    top: 0;
    left: 0;

    color: white;
    background-color: #303033;
    
    font-family: 'Nunito', sans-serif;
    font-weight: 1000;
}

.header > * {
    text-align: center;
}


@media screen and (max-width: 640px) {
    .header {
        font-size: 1rem;
        padding: 10px;
    }
}