* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    width: 100%;
    height: 100vh;

    background-image: url('../assets/background.jpg');
    background-size: cover;
    background-attachment: fixed;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;

    font-family: 'Courier New', Courier, monospace;
}

