.todo {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-flow: column nowrap;

    width: 100%;

    margin: 10px 0;

    color: black;
    background-color: white;

    border: 1px solid grey;
    border-radius: 5px;

    cursor: pointer;

    overflow: visible;

}

.todo:hover {
    /* box-shadow: 2px 2px 2px 0 black; */
}


.row {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    gap: 10px;

    width: 100%;
    padding: 10px;
}

.row > * {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
}


.todo-delete {
    color: rgb(186, 0, 0);
}




.todo-heading {
    /* Overriding display property */
    display: block;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    width: 100%;
}

.todo-discription {
    position: absolute;

    left: 0;
    top: 110%;


    width: 100%;
    padding: 10px;

    font-size: .8rem;

    color: white;
    background-color: rgb(74, 74, 74);

    border-radius: 5px;

    z-index: 1;

    visibility: hidden;
    opacity: 0;
}




.row:hover  + .todo-discription {
    visibility: visible;
    opacity: 1;
    transition: visibility 0s linear 0s, opacity 300ms;
}


/* Toggling classes */
.completed {
    text-decoration: line-through;
    background-color: rgb(214, 214, 214);
}

.hidden {
    display: none;
}



